<template>
  <div class="contents_container" style="border: 1px solid lightgray;">
    <PlantSearch></PlantSearch>
    <PlantList
                v-bind:plantList="dataPlantList" 
                      :dataKeywordPlantList="dataKeywordPlantList"
                      :showKeywordPlantListYn="keywordPlantListYn"
                      :plantKeywordClicked="plantKeywordClicked"
                v-on:plantClicked="showPlantContent"
                v-on:setAllPlantList="showAllPlantList"
                v-on:sendKeywordPlantList="showKeywordPlantList">
    </PlantList>
  </div>
  <div class="contents_container">
    <PlantContent
                    v-bind:plantInfo="paramPlantInfo">
    </PlantContent>
    <PlantClassification 
                          v-bind:plantInfo="paramPlantInfo" 
                                :dataKeywordPlantList="dataKeywordPlantList"
                          v-on:sendKeywordPlantList="showKeywordPlantList">
    </PlantClassification>
    <PlantGuide
                v-bind:plantInfo="paramPlantInfo">
    </PlantGuide>
  </div>
</template>

<script>
import PlantSearch from './PlantSearch.vue';
import PlantList from './PlantList.vue';
import PlantContent from './PlantContent.vue';
import PlantClassification from './PlantClassification.vue';
import PlantGuide from './PlantGuide.vue';
import plantListJson from '../../assets/plantList.json'; // select get_result_json(17);
import plantKeywordPlant from '../../assets/plantKeywordPlant.json'; // select get_result_json(27);
const dataKeywordPlantList = plantKeywordPlant;
  
const dataPlantList = {};
const paramPlantInfo = [];
const keywordPlantListYn = false;

const plantKeywordClicked = '';

export default {
  props: ['plantList'], 
  components: {
    PlantSearch,
    PlantList,
    PlantContent,
    PlantClassification,
    PlantGuide,
  },
  data() {
    return {
      dataPlantList, 
      paramPlantInfo,
      dataKeywordPlantList,
      keywordPlantListYn,
      plantKeywordClicked,
    };
  },
  created() {
    this.showAllPlantList();
  },
  mounted() {

  },
  methods: {
    showPlantContent(plantInfo) {
      // console.log('in showPlantContent 1 ', plantInfo);
      this.paramPlantInfo = plantInfo;
      // console.log('in showPlantContent 2 ', this.paramPlantInfo);
    },   
    showKeywordPlantList(plantKeywordVw, paramPlantKeyword) {
      // 선택한 키워드코드와 일치하는 작물 목록을 담는다.
      
      const keywordPlantList = {};
      this.dataPlantList = {};
      // this.dataKeywordPlantList = {};
      var dataPlantListTmp = plantListJson;
      var idx = 0;

      for(var i=0; i < paramPlantKeyword.length; i++) {
        for(var j=0; j < dataPlantListTmp.length; j++) {
          // console.log('plantList[i] == dataPlantList[j].plnt_id = ', plantList[i] == dataPlantListTmp[j].plnt_id);
          if (paramPlantKeyword[i].plnt_id == dataPlantListTmp[j].plnt_id) {
            keywordPlantList[idx] = dataPlantListTmp[j];
            idx++;
          }
        }
      }

      this.plantKeywordClicked = plantKeywordVw;
      this.dataPlantList = keywordPlantList;
      this.keywordPlantListYn = true;
    },

    showAllPlantList() {
      this.dataPlantList = plantListJson;
      this.keywordPlantListYn = false;
    }
    
  },
};
</script>